html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Oswald", sans-serif;
  font-size: 1.6rem;
  /* &.fijar-body {
        overflow: hidden;
    } */
}

p {
  margin: 0;
  font-size: 2rem;
  color: #000000;
}

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
}

h1,
h2,
h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.bg-yellow {
  background-color: #ffe227;
}

.bg-red {
  background-color: red;
}

.header {
  position: relative;
  background-color: #000000;
}

.header .content-header {
  padding: 2.5rem 0;
}

@media (min-width: 480px) {
  .header .content-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

.header h1 {
  color: #FFFFFFB3;
  margin-bottom: 1rem;
  text-align: center;
}

.header h1:hover {
  color: #FFFFFF;
  cursor: pointer;
}

@media (min-width: 768px) {
  .header h1 {
    margin-bottom: 0;
  }
}

.fijo {
  position: fixed;
  width: 100%;
  box-shadow: 0 .3rem .4rem #4444;
}

.main-navigation {
  display: flex;
  gap: 2rem;
  justify-content: center;
}

.main-navigation a {
  color: #FFFFFFB3;
  font-size: 1.3rem;
  text-transform: uppercase;
  font-weight: 700;
}

.main-navigation a:hover {
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .main-navigation a {
    font-size: 1.7rem;
  }
}

.video {
  position: relative;
  max-height: 90rem;
  overflow: hidden;
}

.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#ff1a00+1,ff1a00+100&0.22+0,0.72+100 */
  background: -moz-linear-gradient(-45deg, rgba(255, 26, 0, 0.22) 0%, rgba(255, 26, 0, 0.23) 1%, rgba(255, 26, 0, 0.72) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, rgba(255, 26, 0, 0.22) 0%, rgba(255, 26, 0, 0.23) 1%, rgba(255, 26, 0, 0.72) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(255, 26, 0, 0.22) 0%, rgba(255, 26, 0, 0.23) 1%, rgba(255, 26, 0, 0.72) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#38ff1a00', endColorstr='#b8ff1a00',GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
}

.video video {
  display: block;
  width: 100%;
}

.content-video {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 10;
}

.content-video h2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: #FFFFFF;
  font-size: 4.3rem;
}

@media (min-width: 768px) {
  .content-video h2 {
    font-size: 6rem;
  }
}

.content-video h2 span {
  background-color: #000000;
  font-size: 2rem;
  padding: 1rem 2rem;
}

.about-festival {
  padding: 2rem 0;
}

@media (min-width: 768px) {
  .about-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

@media (min-width: 768px) {
  .content-festival {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 0.8rem;
  }
}

.content-festival h2 {
  font-size: 3rem;
}

.content-festival .date {
  color: rgba(255, 25, 0, 0.562);
  font-weight: 500;
}

.content-festival .text {
  line-height: 1.5;
}

.lineup {
  background-color: #0C0C0C;
  padding: 5rem 0;
}

.lineup h3,
.lineup p {
  text-align: center;
  color: #FFFFFF;
}

.lineup .day {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .stage-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.stage-container .stage {
  padding-top: 2.5rem;
}

.stage-container .stage-name {
  color: #000000;
  font-size: 2rem;
  font-weight: 700;
}

.stage-container .stage-name:after {
  content: '';
  display: block;
  width: 20rem;
  height: 10rem;
  margin: 2rem auto 0 auto;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center;
}

.stage-name:after {
  background-image: url("/build/img/icono_edm.png");
}

.calendar {
  padding: 0;
  list-style: none;
}

.calendar li {
  padding: 2.5rem;
  font-weight: 700;
}

.calendar li span {
  font-weight: 400;
}

/* odd --> todos los numeros impares */
.bg-red .calendar li:nth-child(odd) {
  background-color: #cc0000;
}

.bg-yellow .calendar li:nth-child(odd) {
  background-color: #f3d200;
}

.gallery {
  padding: 5rem 0;
}

.gallery h3 {
  text-align: center;
}

.gallery-images {
  list-style: none;
  padding: 0;
  margin-top: 5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}

@media (min-width: 768px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2.5rem;
  }
}

@media (min-width: 1200px) {
  .gallery-images {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2.5rem;
  }
}

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center;
}

.btn-close {
  width: 5rem;
  height: 5rem;
  background-color: #FFFFFF;
  display: grid;
  place-items: center;
  font-weight: 700;
  font-size: 3rem;
  border-radius: 50%;
}

.btn-close:hover {
  cursor: pointer;
}

.tickets {
  background-color: #0C0C0C;
  padding: 5rem 0;
}

.tickets h3 {
  text-align: center;
  color: #FFFFFF;
}

.tickets .container-tickets {
  margin-top: 2.5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}

.tickets .plan p {
  color: #FFFFFF;
  font-size: 3rem;
  text-align: center;
  margin-bottom: 2rem;
}

.pass {
  width: 100%;
  border-spacing: 0;
}

.pass thead {
  background-color: #752F97;
}

.pass thead th {
  padding: 1.25rem;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 400;
  font-size: 2rem;
}

.pass tbody {
  background-color: #FFFFFF;
}

.pass tbody td {
  padding: 1.25rem;
  border-bottom: 1px solid #0C0C0C;
}

.pass tbody tr:last-of-type td {
  border: none;
}

.pass tfoot {
  background-color: #ffe227;
}

.pass tfoot td {
  padding: 1.25rem;
  text-align: center;
  font-size: 3rem;
  color: #FFFFFF;
}

.footer {
  background-color: #000000;
  padding: 1.25rem;
  text-align: center;
}

.footer p {
  color: #FFFFFFB3;
  margin: 0;
}
